import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
// import { exportPrizeNotes } from 'services/addPrizeNote';
// import BtnExport from 'components/common/button/BtnExport';
import './style.scss';

@Form.create({ name: 'formFilterScholarship' })
class FormFilterScholarship extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    udpateQueryParams();
  };

  // handleExport = async ({ trainTypeId, semesterId, yearEduId }) => {
  //   try {
  //     await exportPrizeNotes({
  //       trainTypeId,
  //       semesterId,
  //       yearEduId,
  //     });
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  render() {
    const { getFieldDecorator } = this.props.form;
    // const listParamSearch = this.state.paramSearch;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        <Col span={6}>
          <Form.Item label="M SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="H tn">
            {getFieldDecorator('studentFullName', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="">
            <BtnSearch htmlType="submit" />
            <BtnClear onClick={this.handleReset} />
            {/* <BtnExport
              title="Xut Excel"
              onClick={() =>
                this.handleExport({
                  trainTypeId: listParamSearch.trainTypeId,
                  semesterId: listParamSearch.semesterId,
                  yearEduId: listParamSearch.yearEduId,
                })
              }
            /> */}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormFilterScholarship;



